.block {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.link {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  margin-right: 0;
  padding: 30px 0;
  width: 100%;

  font-size: 16px;

  color: black;
  background-color: rgb(113, 113, 225);

  @media screen and (min-width: 768px) {
    margin-right: 30px;
    width: calc((100% - 2 * 30px) / 3);
    &:nth-child(3n) {
      margin-right: 0;
    }
  }

  @media screen and (min-width: 1024px) {
    flex-basis: calc((100% - 3 * 30px) / 4);
    &:nth-child(3n) {
      margin-right: 30px;
    }
    &:nth-child(4n) {
      margin-right: 0;
    }
  }
}
